import React, { useState } from 'react';
import './App.css';

function MyButton({ text }) {
  const [ count, setCount ] = useState(0);

  function onClick() {
    setCount(count + 1);
    alert(`${text} 버튼은 ${count}번 눌렸습니다.`);
  }
  return (
    <button className='sans' onClick={onClick}> {text} </button>
  );
}

function App() {
  return (
    <center>
      <h1>와 리액트!</h1>
      <MyButton text='SANS'/>
      <MyButton text='WOW'/>
      <p>
        으악 CORS
      </p>
    </center>
  );
}

export default App;
